export const mt = (val) => {

    return (""+val).length < 2 ? "0"+val : val;
}

export const getTotals = (table, items) => {
    
    let colspan = 0;
    switch(table){
        case 'checkout': colspan = 4; break;
        case 'account': colspan = 3; break;
    }

    let html = ``;

    // defaults
    let price = { grand_total: 0, total: 0, discount_percent: 0, discount_total: 0, fee_total: 0, tax_total: 0, tax_percent: 0 };

    // sub total
    items.forEach(item => price.total += parseFloat(item.total));

    // grand total
    price.grand_total = price.total;

    // subtotal
    html += `<tr><td colspan="${colspan}"><div class="price" style="margin-top:16px;">${ __html('Subtotal') } <b>${ priceFormat(price.total) }</b></div></td></tr>`;

    // service charge
    if(config.price.fee_calc){
 
        price.fee_total = parseFloat((config.price.fee_percent) * price.total)/100;
        price.fee_percent = config.price.fee_percent;

        html += `<tr><td colspan="${colspan}"><div class="price" >${ __html(config.price.fee_display) } <b>${ priceFormat(price.fee_total) }</b></div></td></tr>`;

        price.grand_total += price.fee_total;
    }
     
    // tax 
    if(config.price.tax_calc){
 
        price.tax_total = ((price.fee_total + price.total) * parseFloat(config.price.tax_percent)/100);
        price.tax_percent = config.price.tax_percent;

        html += `<tr><td colspan="${colspan}"><div class="price ">${ config.price.tax_display } <b>${ priceFormat(price.tax_total) }</b></div></td></tr>`;

        price.grand_total += price.tax_total;
    }
 
    // grand total
    price.grand_total = priceNorm(price.grand_total);

    html += `<tr><td colspan="${colspan}"><div class="price " style="font-size:19px;font-weight:700;">${ __html('Total') } <b>${ priceFormat(price.grand_total) }</b></div></td></tr>`;
   
    return { price: price, html: html };
}

export const getPrice = (product) => {

    // price
    let price = { html: null, priced: null };
    price.html = '<span class="tag ptag">'+priceFormat(product['price'])+'</span>';

    // simple discount
    if(product['priced']!='' && product['priced']!='undefined'){

    //     console.log("priced");
    //     price.html = '<span class="ptagc">'+priceFormat(product['price'])+'</span> <span class="tag ptag">'+priceFormat(product['priced'])+'</span>';
    //     price.priced = product['priced'];
    }
     
    // complex discount
    if(product['discounts']){

        let discounted = false;

        const d = new Date();
        let month = mt(d.getMonth() + 1); //months from 1-12
        let day = mt(d.getDate());
        let year = mt(d.getFullYear());

        product['discounts'].forEach(el => {

            switch(el.availability){

                case 'always':

                   discounted = true;

                break;
                case 'weekly':

                    // console.log(d.getDay());

                    if(el.dow[d.getDay()-1]){ discounted = true; }else{ break; }

                case 'hourly':
                
                    let from = new Date(month + "/" + day + "/" + year + ' ' + el.hours.from + ':00').getTime();
                    let to = new Date(month + "/" + day + "/" + year + ' ' + el.hours.to + ':00').getTime();

                    // console.log(from + " " + Date.now() / 1000 + " " + to);

                    if(from >  Date.now() / 1000 | 0 > to){
                       
                       discounted = true;
                    }

                break;
            }

            if(discounted){

                // discount type
                if(el.type == 'percent'){

                    // alert(((100-el.percent)/100));
                    price.priced = makeNumber(product['price']) * ((100-el.percent)/100);

                }else if(el.type == 'value'){

                    price.priced =  el.value;
                }

                price.html = `<span class="ptagc">${ priceFormat(product['price']) }</span> <span class="tag ptag">${ priceFormat(price.priced) }</span>`;

                if(el.note) price.html += `<div class="pricenote">${ el.note }</div>`;
            }
            // console.log(el);
        });
    }

    return price;
}

/**
 * @name setCookie
 * @description Set cookie by its name to all .kenzap.com subdomains
 * @param {string} name - Cookie name.
 * @param {string} value - Cookie value.
 * @param {string} days - Number of days when cookie expires.
 */
export const setCookie = (name, value, days) => {

    let expires = "";
    if (days) {
        let date = new Date();
        date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
        expires = ";expires=" + date.toUTCString();
    }
    document.cookie = name + "=" + (escape(value) || "") + expires + ";path=/"; 
}

/**
 * @name getCookie
 * @description Read cookie by its name.
 * @param {string} cname - Cookie name.
 * 
 * @returns {string} value - Cookie value.
 */
export const getCookie = (cname) => {

    let name = cname + "=";
    let decodedCookie = decodeURIComponent(document.cookie);
    let ca = decodedCookie.split(';');
    for (let i = 0; i < ca.length; i++) {
        let c = ca[i];
        while (c.charAt(0) == ' ') {
            c = c.substring(1);
        }
        if (c.indexOf(name) == 0) {
            return c.substring(name.length, c.length);
        }
    }
    return "";
}

/**
    * Normalize price to keep two decimal points
    * @public
    */
export const priceNorm = function(price) {

    price = makeNumber(price);

    let priceF = (Math.round(parseFloat(price) * 100)/100).toFixed(2);
    
    return priceF;
}


/**
    * Render price
    * @public
    */
export const priceFormat = function(price) {

    price = makeNumber(price);

    let priceF = priceNorm(price);
    
    switch(config.price.style){
        case 'left': priceF = config.price.symbol + priceF; break;
        case 'right': priceF = priceF + config.price.symbol; break;
    }

    return priceF;
}

/**
    * Normalize number variables mostly to cover malformed data cases
    * @public
    */
export const makeNumber = function(price) {

    price = price == "" ? 0 : price;

    return price;
}

/**
    * Generates a random string
    * 
    * @param int length_
    * @return string
    */
export const randomString = function(length_) {

    var chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghiklmnopqrstuvwxyz'.split('');
    if (typeof length_ !== "number") {
        length_ = Math.floor(Math.random() * chars.length_);
    }
    var str = '';
    for (var i = 0; i < length_; i++) {
        str += chars[Math.floor(Math.random() * chars.length)];
    }
    return str;
}

/**
    * Convert text to url friendly slug 
    * @public
    */
export const convertToSlug = function(str) {

    let output = "cat";
    for(let i=0; i<str.length-1; i++){

        output += str.charCodeAt(i)
    }

    return output;
    // return str
    //     .toLowerCase()
    //     .replace(/[^\w ]+/g,'')
    //     .replace(/ +/g,'-')
    //     ;
}

/**
    * trigger small vibration effect when user interacts with UI
    * @public
    */
export const vibrate = function(){

    if (window.navigator && window.navigator.vibrate) {
        // Vibrate once for 1 second
        navigator.vibrate(20);
    }
}

/**
 * Show small toast notification
 * @public
 */
export const toast = function(msg){

    // Get the snackbar DIV
    let t = document.querySelector(".kUNwHA .snackbar");
    t.innerHTML = msg;
    t.classList.add("show");

    // After 3 seconds, remove the show class from div
    setTimeout(function(){ t.className = t.className.replace("show", ""); }, 2200);
}

 export const renderStatus = (__, status) => {

    switch (status){

        case 'new': return '<div class="badge btn-warning text-dark fw-light">' + __html('New') + '</div>';
        case 'processing': return '<div class="badge btn-warning text-dark fw-light">' + __html('Cooking') + '</div>';
        case 'paid': return '<div class="badge btn-warning text-dark fw-light">' + __html('New') + '</div>';
        case 'completed': return '<div class="badge btn-success text-dark fw-light">' + __html('Served') + '</div>';
        case 'canceled': return '<div class="badge btn-secondary text-dark fw-light">' + __html('Canceled') + '</div>';
        case 'refunded': return '<div class="badge btn-secondary text-dark fw-light">' + __html('Refunded') + '</div>';
        case 'failed': return '<div class="badge btn-secondary text-dark fw-light">' + __html('Failed') + '</div>';
        default: return '<div class="badge btn-primary text-dark fw-light">' + __html('Processing') + '</div>';

    }
 }

 // time elapsed since creation 
 export const timeConverterAgo = (__, now, time) => {

    // console.log(now + " " + time);

    now = parseInt(now);
    time = parseInt(time);

    // console.log(now + " " + time);

    // parse as elapsed time
    let past = now - time;
    if(past < 60) return __html('moments ago');
    if(past < 3600) return parseInt(past / 60) + ' ' + __html('minutes ago');
    if(past < 86400) return parseInt(past / 60 / 24) + ' ' + __html('hours ago');

    // process as normal date
    var a = new Date(time * 1000);
    var months = [__html('Jan'), __html('Feb'), __html('Mar'), __html('Apr'), __html('May'), __html('Jun'), __html('Jul'), __html('Aug'), __html('Sep'), __html('Oct'), __html('Nov'), __html('Dec')];
    var year = a.getFullYear();
    var month = months[a.getMonth()];
    var date = a.getDate();
    var hour = a.getHours();
    var min = a.getMinutes();
    var sec = a.getSeconds();
    var time = date + ' ' + month + ' ' + year; // + ' ' + hour + ':' + min + ':' + sec ;
    return time;
 }

/*
* Load additional JS or CSS depencies
*
* @param    dep       dependecy. Ex.: hiebee.min.js 
* @param    cb        function to call after script is loaded (optional)       
* @return 	{Boolen} 	result status 
* 
*/
export const loadAddon = (dep, t, version, cb) => {

    // dependency already loaded, skip
    if(document.getElementById(dep)){ if(typeof cb === 'function') cb.call(); return; }

    // detect dependency type
    // let t = dep.split('.').slice(-1)[0];
    // console.log(dep);
    // console.log('loadAddon'+t);
    switch(t){
      case 'js':
        
        let js = document.createElement("script");
        js.setAttribute("src", dep);
        js.id = "addon"+version;
        js.onload = js.onreadystatechange = function() {
 
          if(!this.readyState || this.readyState == 'complete')
            if(typeof cb === 'function') cb.call();
        };
        document.body.appendChild(js);
        
      break;
      case 'css':

        let head  = document.getElementsByTagName('head')[0];
        let css  = document.createElement('link');
        css.id   = dep;
        css.rel  = 'stylesheet';
        css.type = 'text/css';
        css.href = dep;
        head.appendChild(css);

      break;
    }
}