// dependencies
import { H, showLoader, hideLoader, initHeader, initFooter, initBreadcrumbs, parseApiError, onClick, onChange, simulateClick, spaceID, toast, link, __, __attr, __html } from '@kenzap/k-cloud';
import { getCookie, languages } from "../_/_helpers.js"
import { HTMLContent } from "../_/_cnt_home.js"
import qrcode from 'qrcode-generator-es6';

// where everything happens
const _this = {

    state: {
        firstLoad: true,
        html: '',
        data: {},
        ecommerce_settings: {},
        available_locales: [],
        tldType: '.kenzap.site',
        newQR: false,
        baseURL: '',
        downloadName: 'qr-menu.svg',
        API_key: '',
        ajaxQueue: 0
    },
    init: () => {
        
        _this.getData();
        
        _this.getQRHTML();

        _this.getQRJS();

        _this.getQRCSS();
    },
    getData: () => {

        // show loader during first load
        if (_this.state.firstLoad) showLoader();

        // do API query
        fetch('https://api-v1.kenzap.cloud/', {
            method: 'post',
            headers: H(),
            body: JSON.stringify({
                query: {
                    keys: {
                        type:       'api-key',
                        keys:       ['121']
                        // Frontend / Read & write / Isolated
                    },
                    locale: {
                        type:       'locale',
                        source:      ['extension'],
                        key:         'qr-menu',
                    },
                    settings: {
                        type:       'get', 
                        key:        'qrmenu-settings',
                        fields:     ['mode', 'palette', 'categories', 'locales', 'max_addition', 'slug', 'total_tables', 'table_number', 'aggregation', 'orders_cancelable', 'fast_checkout', 'payment_method', 'checkout_takeaway_field', 'checkout_takeaway_phone_field', 'checkout_takeaway_address_field', 'checkout_notes_field', 'orders_call_waiter', 'updated'],
                    },
                    ecommerce_settings: {
                        type:       'get',
                        key:        'ecommerce-settings',
                        fields:     ['currency', 'currency_symb', 'currency_symb_loc', 'tax_calc', 'tax_auto_rate', 'tax_percent', 'tax_display', 'fee_calc', 'fee_percent', 'fee_display', 'add_products', 'add_products_list',],
                    },
                    available_locales: {
                        type:       'find', 
                        key:        'locale-qr-menu',
                        fields:     ['_id', 'language', 'location', 'locale', 'ext', 'updated'],
                    },
                }
            })
        })
        .then(response => response.json())
        .then(response => {

            // hide UI loader
            if(response.success){

                // init header
                initHeader(response);

                // get core html content 
                _this.loadHomeStructure();  

                // render table
                _this.renderPage(response);

                // listeners
                _this.initListeners();

                // initiate footer
                _this.initFooter();

                // first load
                _this.state.firstLoad = false;

            }else{

                parseApiError(response);
            }
        })
        .catch(error => {
            console.error('Error:', error);
        });
    },
    getQRHTML: () => {

        // do API query
        fetch('/feed/index.html', {
            method: 'get',
            headers: {
                // 'Accept': 'application/json',
                // 'Content-Type': 'text/plain',
                // 'Authorization': 'Bearer ' + getCookie('kenzap_api_key'),
                // 'Kenzap-Header': localStorage.hasOwnProperty('header'),
                // 'Kenzap-Token': getCookie('kenzap_token'),
                // 'Kenzap-Sid': spaceID(),
            }
        })
        .then(response => response.text())
        .then(response => {

            _this.state.html = response;
        })
        .catch(error => {
            console.error('Error:', error);
        });
    }, 
    getQRJS: () => {

        // do API query
        fetch('/feed/index.js', {
            method: 'get',
            headers: {
                // 'Accept': 'application/json',
                // 'Content-Type': 'text/plain',
                // 'Authorization': 'Bearer ' + getCookie('kenzap_api_key'),
                // 'Kenzap-Header': localStorage.hasOwnProperty('header'),
                // 'Kenzap-Token': getCookie('kenzap_token'),
                // 'Kenzap-Sid': spaceID(),
            }
        })
        .then(response => response.text())
        .then(response => {

            _this.state.js = response;
        })
        .catch(error => {
            console.error('Error:', error);
        });
    }, 
    getQRCSS: () => {

        // do API query
        fetch('/feed/styles.css', {
            method: 'get',
            headers: {
                // 'Accept': 'application/json',
                // 'Content-Type': 'text/plain',
                // 'Authorization': 'Bearer ' + getCookie('kenzap_api_key'),
                // 'Kenzap-Header': localStorage.hasOwnProperty('header'),
                // 'Kenzap-Token': getCookie('kenzap_token'),
                // 'Kenzap-Sid': spaceID(),
            }
        })
        .then(response => response.text())
        .then(response => {

            _this.state.css = response;
        })
        .catch(error => {
            console.error('Error:', error);
        });
    }, 
    renderPage: (response) => {

        // initiate breadcrumbs
        initBreadcrumbs(
            [
                { link: link('https://dashboard.kenzap.cloud?launcher=qr-menu'), text: __html('Dashboard') },
                { text: __html('QR menu') },
            ]
        );

        _this.state.data = response.settings;
        _this.state.ecommerce_settings = response.ecommerce_settings;
        _this.state.available_locales = response.available_locales;

        // console.log(response.settings);

        // console.log(_this.state.available_locales);

        // render avalable languages
        let languages_html = "";
        _this.state.available_locales.forEach((lang, i) => {

            let l = languages.filter((obj) => { return obj.code === lang.language });

            _this.state.available_locales[i].language = l[0].name;
            _this.state.available_locales[i].language_native = l[0].native ? __html(l[0].native) : l[0].name;

            languages_html += `<span data-code="${ lang }" data-language="${ l[0].name }" data-language-native="${ __attr(l[0].native) }" class="badge rounded-pill me-2 mb-2 bg-primary fs-6">${ __html(l[0].name) }</span>`;
        });

        document.querySelector("#lang-badges").innerHTML = languages_html;

        // populate fields
        for (let field in response.settings){

            if(typeof(response.settings[field]) === "undefined") continue;
            if(response.settings[field] == "") continue;
            if(document.querySelector("#"+field)) switch(document.querySelector("#"+field).dataset.type){
        
                case 'text':
                case 'email':
                case 'emails':
                case 'number':
                case 'select':
                case 'textarea': document.querySelector("#"+field).value = response.settings[field]; break;
                case 'range': 
                
                    document.querySelector("#"+field).value = response.settings[field];
                    document.querySelector("#"+field+'_val').innerHTML = response.settings[field];
                break;
                case 'palette': 

                    for(let p in response.settings[field]){

                        document.querySelector('[data-key="'+p+'"]').value = response.settings[field][p];
                        document.querySelector('[data-key="'+p+'"]').style.backgroundColor = response.settings[field][p];
                    }
                
                break;
                case 'radio':
                    if(response.settings[field]) document.querySelector("#"+field+response.settings[field]).setAttribute('checked', true);
                break;
                case 'checkbox':
                    if(response.settings[field]) document.querySelector("#"+field).setAttribute('checked', response.settings[field] == 'true' ? true : false);
                break;

            }
        }

        _this.listeners.modeRefresh();

        hideLoader();
    },
    initListeners: () => {

        // rename layout
        onClick('.rename-layout', _this.listeners.renameLayout);

        // duplicate layout
        onClick('.duplicate-layout', _this.listeners.duplicateLayout);

        // rename layout
        onClick('.remove-layout', _this.listeners.removeLayout);

        // download QR codes
        onClick('.qr-download', _this.listeners.qrDownload);

        // pallete listener
        onChange('.palette input', _this.listeners.paletteRefresh);
        
        // max_addition
        onChange('#max_addition', _this.listeners.maxAdditionRange);

        // total tables number changed
        onChange('#total_tables', _this.listeners.totalTablesRefresh);

        // table number changed
        onChange('#table_number', _this.listeners.tableNumberRefresh);

        // max_addition
        onChange('[name="mode"]', _this.listeners.modeRefresh);

        // conditional takeaway checkbox setting
        onChange('#checkout_takeaway_field', _this.listeners.toggleTakeaway); _this.listeners.toggleTakeaway();

        // prevents listeners to be assigned twice
        if(!_this.state.firstLoad) return;

        // add layout listener
        onClick('.btn-publish', _this.listeners.publish);

        // domain change listener
        onClick('.domain-list li a', _this.listeners.domainChange);
        
        // add product confirm
        onClick('.btn-modal', _this.listeners.modalSuccessBtn);

        // check hash request preview
        _this.listeners.hashRequest();

        // set input field defaults
        _this.listeners.setDefaults();
    },
    listeners: {

        publish: (e) => {

            e.preventDefault();

            // validate slug
            if(document.querySelector('#slug').value.length<4){ alert( __html('Please provide a longer public link') ); return; }

            // validate categories
            if(document.querySelector('#categories').value.length<3){ alert( __html('Please provide at least one menu category') ); return; }

            // do API query
            showLoader();

            let data = {};

            // iterate through all fields
            for(let s of document.querySelectorAll('.inp')){
    
                switch(s.dataset.type){
              
                    case 'text':   
                    case 'email':  
                    case 'emails':  
                    case 'number':
                    case 'select':
                    case 'textarea': data[s.id] = s.value; break;
                    case 'range': 
                        data[s.id] = s.value;
                    break;
                    case 'radio': data[s.getAttribute('name')] = s.parentElement.parentElement.parentElement.querySelector('input:checked').value; break;
                    case 'checkbox': data[s.getAttribute('name')] = s.checked; break;
                    case 'palette': 

                        if(data[s.id] == null) data[s.id] = {};
                        for(let p of s.querySelectorAll('input')){

                            data[s.id][p.dataset.key] = p.value;
                        }
                     
                    break;
                }
            }

            // QR-code changed, refresh it and update the user
            if(_this.state.data.mode != data.mode) _this.state.newQR = true;
            if(_this.state.data.slug != data.slug) _this.state.newQR = true;

            _this.state.data = data;

            _this.preProcessHTML();

            // send data
            fetch('https://api-v1.kenzap.cloud/', {
                method: 'post',
                headers: H(),
                body: JSON.stringify({
                    query: {
                        settings: {
                            type:       'set',
                            key:        'qrmenu-settings',       
                            data:       data
                        }
                    }
                })
            })
            .then(response => response.json())
            .then(response => {

                if (response.success){

                    toast('Applying changes');

                    if(_this.state.newQR){

                        _this.refreshURL();

                        document.querySelector('.qr-note').classList.remove('d-none');
                    }

                    hideLoader();

                    // _this.getData();

                }else{

                    parseApiError(response);
                }
            })
            .catch(error => {
                console.error('Error:', error);
            });
            
        },
        hashRequest: (e) => {

            if(window.location.hash == '#preview'){

                let link = document.querySelector('.preview-link');
                window.location.replace(link.getAttribute('href'));   
            }

            if(window.location.hash == '#qrcode'){

                let link = document.querySelector('.qr-download');
                simulateClick(link);
            }
        },
        setDefaults: (e) => {

            // set defaults
            if(_this.state.available_locales.length == 0) _this.state.available_locales = [{'language': 'English', 'lang': 'en'}];
            if(document.querySelector("#slug").value == '') document.querySelector("#slug").value = 'menu' + spaceID();
        },
        paletteRefresh: (e) => {

            e.preventDefault();

            e.currentTarget.style.backgroundColor = e.currentTarget.value;
        },
        maxAdditionRange: (e) => {

            document.querySelector('#max_addition_val').innerHTML = e.currentTarget.value;
        },
        totalTablesRefresh: (e) => {

            _this.listeners.genTableSelect();
        },
        tableNumberRefresh: (e) => {

            _this.refreshURL();
        },
        modeRefresh: (e) => {

            let mode = parseInt(document.querySelector('[name="mode"]').parentElement.parentElement.parentElement.querySelector('input:checked').value);
        
            if(mode==1){ 
                
                document.querySelector('.total_tables_cont').classList.remove('d-none'); 
                document.querySelector('.table_number_cont').classList.remove('d-none'); 

                _this.listeners.genTableSelect();
                
            }else{ 
                
                document.querySelector('.total_tables_cont').classList.add('d-none'); 
                document.querySelector('.table_number_cont').classList.add('d-none'); 
            }

            // init QR
            _this.refreshURL();
        },
        genTableSelect: (e) => {

            let total_tables = document.querySelector('#total_tables').value;
            if(total_tables == '') total_tables = 1;
            let options = `<option value="1" selected>${ __html('Table') } #1</option>`;
            for(let i=1; i<total_tables; i++){

                options += `<option value="${ i+1 }">${ __html('Table') } #${ i+1 }</option>`;
            }

            document.querySelector('#table_number').innerHTML = options;
        },
        qrDownload: (e) => {

            e.preventDefault();

            let qr_svg = e.currentTarget.parentNode.querySelector('.qr-preview').innerHTML;

            let svg_data_uri = 'data:image/svg+xml;utf8,' + qr_svg;
            let link = document.querySelector('.d-link');
            link.setAttribute('href', svg_data_uri);
            link.setAttribute('download', _this.state.downloadName);
 
            simulateClick(link);
        },
        domainChange: (e) => {

            e.preventDefault();

            let btn = document.querySelector('.btn-tld');
            btn.innerHTML = e.currentTarget.innerHTML;
            _this.state.tldType = e.currentTarget.dataset.key;
        },
        toggleTakeaway: (e) => {

            // if(document.querySelector("#checkout_takeaway_field").checked){
          
            //     document.querySelector("#checkout_takeaway_phone_field").parentElement.parentElement.parentElement.classList.remove("d-none");
            //     document.querySelector("#checkout_takeaway_address_field").parentElement.parentElement.parentElement.classList.remove("d-none");
                
            // }else{

            //     document.querySelector("#checkout_takeaway_phone_field").parentElement.parentElement.parentElement.classList.add("d-none");
            //     document.querySelector("#checkout_takeaway_address_field").parentElement.parentElement.parentElement.classList.add("d-none");
            // }
        },
        modalSuccessBtn: (e) => {
            
            _this.listeners.modalSuccessBtnFunc(e);
        },
        modalSuccessBtnFunc: null
    },
    preProcessHTML: () => {

        const limit = 1000;

        // let page = getPage(); 
        let offset = 0;
        
        let query = {
            items: {
                type:       'find',
                key:        'ecommerce-product',
                fields:     ['_id', 'img', 'status', 'price', 'priced', 'title', 'cats', 'sdesc', 'ldesc', 'variations', 'updated'],
                limit:      limit,
                offset:     offset,
                sortby:     {
                                field: 'created',
                                order: 'ASC'
                            }
            },
            settings: {
                type:       'get',
                key:        'qrmenu-settings',
                fields:     ['mode', 'palette', 'max_addition', 'slug', 'total_tables', 'table_number', 'aggregation', 'orders_cancelable', 'fast_checkout', 'payment_method', 'checkout_takeaway_field', 'checkout_takeaway_phone_field', 'checkout_takeaway_address_field', 'checkout_notes_field', 'orders_call_waiter', 'categories'],
            }
        };
   
        _this.state.available_locales.forEach((lang, e) => {

            query['locale_'+lang.locale] = {
                type:       'locale',
                locales: [
                    {
                        key: 'qr-menu',
                        source: 'extension',
                        locale: lang.locale,   
                    },
                    {
                        key: 'ecommerce',
                        source: 'extension',
                        locale: lang.locale,
                    },
                    {
                        key: 'ecommerce',
                        source: 'content',
                        locale: lang.locale,
                    }
                ]
            }
        });

        fetch('https://api-v1.kenzap.cloud/', {
            method: 'post',
            headers: H(),
            body: JSON.stringify({
                query
            })
        })
        .then(response => response.json())
        .then(response => {
    
            if (response.success){

                // TODO consider alternative flushing mode
                // _this.flushHTML(response);
                _this.renderMenuHTML(response);
                         
            }else{
    
                parseApiError(response);
            }
        })
        .catch(error => { parseApiError(error); });
    },
    publishMenu: (locale, html, js, css) => {

        let d = document;

        let data = {};

        // publishing data
        data.domain = d.querySelector('#slug').value + _this.state.tldType;

        // dev
        if(window.location.host.indexOf("localhost") == 0){

            data.files = [
                { type:'raw', data:html, name:'index.html' },
                { type:'raw', data:js, name:'index.js' },
                { type:'raw', data:css, name:'styles.css' }
            ];
    
        // production
        }else{

            data.files = [
                { type:'raw', data:html, name:'index.html' },
                { type:'github', url:'https://raw.githubusercontent.com/kenzap/qr-menu/main/public/feed/index.js' },
                { type:'github', url:'https://raw.githubusercontent.com/kenzap/qr-menu/main/public/feed/styles.css' }
            ];

        }

        data.files_etc = [];

        let params = new URLSearchParams();
        params.append("cmd", "publish_site_v2");
        params.append("key", "qrmenu");
        params.append("flush", false);
        params.append("folder", locale);
        params.append("data", JSON.stringify(data));
        params.append("html", html);
        params.append("sid", spaceID());
        params.append("token", getCookie('kenzap_token'));

        // do API query
        fetch('https://siteapi.kenzap.cloud/v1/', {
            method: 'post',
            headers: {
                'Accept': 'application/json',
                'Content-type': 'application/x-www-form-urlencoded',
            },
            body: params
        })
        .then(response => response.json())
        .then(response => {

            // hide UI loader
            hideLoader();

            if(response.success){


            }else{

                parseApiError(response);
            }
        })
        .catch(error => {
            console.error('Error:', error);
        });
    },
    flushHTML: (response) => {

        let d = document;

        let data = {};

        // publishing data
        data.domain = d.querySelector('#slug').value + _this.state.tldType;
        data.files = [];
        data.files_etc = [];

        let params = new URLSearchParams();
        params.append("cmd", "publish_site");
        params.append("key", "qrmenu");
        params.append("flush", true);
        // params.append("folder", locale == 'en' ? '' : locale);
        params.append("folder", "");
        params.append("data", JSON.stringify(data));
        params.append("html", "");
        params.append("sid", spaceID());
        params.append("token", getCookie('kenzap_token'));

        // do API query
        fetch('https://siteapi.kenzap.cloud/v1/', {
            method: 'post',
            headers: {
                'Accept': 'application/json',
                'Content-type': 'application/x-www-form-urlencoded',
            },
            body: params
        })
        .then(response_flush => response_flush.json())
        .then(response_flush => {

            // hide UI loader
            hideLoader();

            if(response_flush.success){

                _this.refreshURL();

                _this.renderMenuHTML(response);

            }else{

                parseApiError(response);
            }
        })
        .catch(error => {
            console.error('Error:', error);
        });
    },
    genQR: (sel, domain) => {

        const qr = new qrcode(0, 'H');
        qr.addData(domain);
        qr.make();
        document.querySelector(sel).innerHTML = qr.createSvgTag({});

    },
    renderMenuHTML: (response) => {

        console.log(response.settings);

        let i = 1;
                _this.state.available_locales.forEach((lang) => {

                    let customizer = `
                        <!-- Customizer start -->
                        <style>
                            :root {
                                --txtColorA: ${ _this.state.data.palette.txtColorA };;
                                --txtColorB: ${ _this.state.data.palette.txtColorB };
                                --txtColorC: ${ _this.state.data.palette.txtColorC };
                                --bgColorA: ${ _this.state.data.palette.bgColorA };
                                --bgColorB: ${ _this.state.data.palette.bgColorB };
                                --bgColorC: #000000;
                                --linkColorA: #1941DF;
                                --linkColorB: #1941dfd0;
                                --baseColorA: ${ _this.state.data.palette.baseColorA };
                                --baseColorB: #1941df;
                                --accColorA: #1941df;
                                --accColorB: #1941df;
                                --grayColorA: #F7F7F7;
                                --grayColorB: #c0c0c0;
                                --grayColorC: #818181;
                            }
                            body{
                                font-family: Poppins, sans-serif
                            }
                        </style>
                        <script>
                            const API_KEY = '${ _this.state.API_key }';
                            let config = {"price":{"fee_calc":"${ _this.state.ecommerce_settings.fee_calc ? _this.state.ecommerce_settings.fee_calc : "" }","fee_percent":"${ _this.state.ecommerce_settings.fee_percent ? _this.state.ecommerce_settings.fee_percent : "" }","fee_display":"${ _this.state.ecommerce_settings.fee_display ? _this.state.ecommerce_settings.fee_display : "" }","tax_calc":"${ _this.state.ecommerce_settings.tax_calc ? _this.state.ecommerce_settings.tax_calc : "" }","tax_percent":"${ _this.state.ecommerce_settings.tax_percent ? _this.state.ecommerce_settings.tax_percent : "" }","tax_display":"${ _this.state.ecommerce_settings.tax_display ? _this.state.ecommerce_settings.tax_display : "" }","currency":"${ _this.state.ecommerce_settings.currency ? _this.state.ecommerce_settings.currency : "USD" }","symbol":"${ _this.state.ecommerce_settings.currency_symb ? _this.state.ecommerce_settings.currency_symb : "$" }","style":"${ _this.state.ecommerce_settings.currency_symb_loc ? _this.state.ecommerce_settings.currency_symb_loc : "left" }"},"cart":{"max_addition":${ _this.state.data.max_addition }},"PREFIX":"/menu","domain":"${ _this.state.baseURL }","fast_checkout":"${ _this.state.data.fast_checkout }","payment_method":"${ _this.state.data.payment_method }","aggregation":"${ _this.state.data.aggregation }","orders_cancelable":"${ _this.state.data.orders_cancelable }","sid":"${ spaceID() }","available_locales":${ JSON.stringify(_this.state.available_locales) },"add_products":"${ _this.state.ecommerce_settings.add_products ? _this.state.ecommerce_settings.add_products : "" }","add_products_list":"${ _this.state.ecommerce_settings.add_products_list ? _this.state.ecommerce_settings.add_products_list : "" }","moreButton":true};
                            let products = ${ JSON.stringify(response.items) };
                            let settings = ${ JSON.stringify(response.settings) };
                            let i18n = {"state":{"locale": ${ JSON.stringify(response['locale_'+lang.locale]) } } };
                        </script>`;

                    let html = _this.state.html.substring(0, _this.state.html.indexOf('<!-- Customizer start -->')) + customizer + _this.state.html.substring(_this.state.html.indexOf('<!-- Customizer end -->'), _this.state.html.length);
                    html = html.replace(/{{timestamp}}/g, (Math.floor(Date.now() / 1000)));
                    
                    setTimeout((locale, html)=>{

                        _this.publishMenu(locale, html, _this.state.js, _this.state.css);

                    }, i*100, lang.locale, html);
                    i++;
                });

                toast('Menu published');
    },
    refreshURL: () => {

        // table mode
        let mode = parseInt(document.querySelector('[name="mode"]').parentElement.parentElement.parentElement.querySelector('input:checked').value);
        
        // table number
        let tn = document.querySelector('#table_number').value;
        if(tn == '') tn = 1;

        let postfix = '';
        _this.state.downloadName = 'qr-menu.svg';
        switch(mode){
            case 0: 

            break;
            case 1: 
                postfix = '?table='+tn;
                _this.state.downloadName = 'qr-menu-table-'+tn+'.svg';
            break;
            case 2: 
                postfix = '?table=0';
                _this.state.downloadName = 'qr-menu-no-table-mode.svg';
            break;
        }

        _this.state.baseURL = 'http://' + _this.state.data.slug + _this.state.tldType + postfix;
    
        // array 
        document.querySelector('.p-links').innerHTML = "";

        let burlqr = "";
        _this.state.available_locales.forEach((lang, i) => {
            
            let l = 'http://' + _this.state.data.slug + _this.state.tldType + '/' + lang.locale + '/' + postfix; 

            if(burlqr=="") burlqr = l;

            _this.state.available_locales[i].link = l
            let link = '<li><a target="_blank" data-locale="' + lang.locale + '" href="' + l + '">' + l + '</a></li>';
            let div = document.createElement('div');
            div.innerHTML = link
            document.querySelector('.p-links').append(div);
        });

        document.querySelector('.preview-link').setAttribute('href', burlqr);

        _this.genQR('.qr-preview', burlqr);
    },
    loadHomeStructure: () => {

        if(!_this.state.firstLoad) return;

        // get core html content 
        document.querySelector('#contents').innerHTML = HTMLContent();
    },
    initFooter: () => {
        
        initFooter( __html('Created by %1$Kenzap%2$. ❤️ Licensed %3$GPL3%4$.', '<a class="text-muted" href="https://kenzap.com/" target="_blank">', '</a>', '<a class="text-muted" href="https://github.com/kenzap/qr-menu" target="_blank">', '</a>'), '');
    }
}

_this.init();
